body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}

.App {
  text-align: center;
}

.title {
  text-align: center;
  font-size: 2em;
  background: purple;
  color: #eaf3ff;
}

.title:hover {
  cursor: pointer;
  color: aqua;
  transition: color 2s;
}

.text {
  font-size: 18px;
  margin: 20px 0;
  padding-left: 20px;
  text-align: left;
}

.list {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  list-style: none;
}

.item {
  margin: 5px;
  background-color: aqua;
  width: 400px;
  padding: 10px;
  font-size: 16px;
  margin-bottom: 10px;
}

.item:hover {
  transition: all 1s;
  padding: 25px;
  background-color: aqua;
  cursor: pointer;
}

.list-alter {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  list-style: none;
}

.item-alter {
  background-color: royalblue;
  text-align: center;
  margin: 10px 0;
  width: 400px;
  padding: 10px;
}

.item-alter:hover {
  background-color: yellow;
  transition: all 1s;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  .item {
    background: salmon;
  }

  .item:hover {
    transition: all 1s;
    padding: 25px;
    background-color: salmon;
    cursor: pointer;
  }
}

@media screen and (max-width: 425px) {
  .list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }
}

@media screen and (max-width: 375px) {
  .list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  .item {
    -webkit-align-items: center;
            align-items: center;
    width: 330px;
  }
}

@media screen and (max-width: 320px) {
  .list {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
  }

  .item-alter:hover {
    background-color: green;
    transition: all 1s;
    cursor: pointer;
  }
}

